<template>
  <div class="content">
    <section class="line" v-for="(item, index) of msg" :key="index" @click="check(index)">
      <van-icon
        name="comment-circle"
        size="30"
        :color="item.isRead ? '#0867E9':'#6CAFF5'"
        :dot="item.isRead"
      />
      <div :class="item.isRead ? 'rightInfo':'rightInfoFalse'">
        <p>{{item.title}}</p>
        <p>{{item.createdTime}}</p>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      msg: [
        { title: "通知消息004", createdTime: "2019-12-12 14:02", isRead: true },
        { title: "通知消息003", createdTime: "2019-12-12 14:02", isRead: true },
        { title: "通知消息000", createdTime: "2019-12-12 14:02", isRead: false }
      ]
    };
  },
  methods: {
    check(index) {
      this.msg.forEach((e, i) => {
        if (i === index) {
          e.isRead = false;
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.content {
  background: $fc;
  font-size: 0.95rem;
  .line {
    @include fr(flex-start);
    margin: 0.8rem;
    padding: 0.5rem 0rem;
    border-bottom: 0.02rem solid $F5;
  }
  .rightInfo {
    text-align: left;
    margin-left: 0.8rem;
    p:last-child {
      font-size: 0.7rem;
      color: $Tline;
      margin-top: 0.2rem;
    }
  }
  .rightInfoFalse {
    text-align: left;
    margin-left: 0.8rem;
    color: $Tline;
    p:last-child {
      font-size: 0.7rem;
      margin-top: 0.2rem;
    }
  }
}
</style>